"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

export interface BusSchedule {
  id: number;
  route: string;
  date: string;
  time: string;
}

interface BusScheduleManagerProps {
  schedules: BusSchedule[];
  onAddSchedule: (schedule: BusSchedule) => void;
}

export default function BusScheduleManager({ schedules, onAddSchedule }: BusScheduleManagerProps) {
  const [route, setRoute] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [message, setMessage] = useState("");
  const [showMessage, setShowMessage] = useState(false);

  const handleAdd = (e: React.FormEvent) => {
    e.preventDefault();
    if (!route || !date || !time) {
      setMessage("Please fill in all schedule fields.");
      setShowMessage(true);
      setTimeout(() => setShowMessage(false), 3000);
      return;
    }
    const newSchedule: BusSchedule = {
      id: Date.now(),
      route,
      date,
      time,
    };
    onAddSchedule(newSchedule);
    setRoute("");
    setDate("");
    setTime("");
    setMessage("Schedule added successfully.");
    setShowMessage(true);
    setTimeout(() => setShowMessage(false), 3000);
  };

  return (
    <section>
      <h2 className="text-3xl font-bold mb-6 text-center">Manage Bus Schedules</h2>
      <form onSubmit={handleAdd} className="max-w-md mx-auto space-y-6">
        <div>
          <Label htmlFor="route">Route</Label>
          <Input
            id="route"
            type="text"
            value={route}
            onChange={(e) => setRoute(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        <div>
          <Label htmlFor="date">Date</Label>
          <Input
            id="date"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        <div>
          <Label htmlFor="time">Time</Label>
          <Input
            id="time"
            type="time"
            value={time}
            onChange={(e) => setTime(e.target.value)}
            required
            className="mt-1"
          />
        </div>
        {showMessage && <p className="text-green-600 text-center animate-fadeIn">{message}</p>}
        <Button type="submit" className="w-full transition-colors hover:bg-black hover:text-white">
          Add Schedule
        </Button>
      </form>

      {schedules.length === 0 ? (
        <p className="mt-8 text-center text-gray-600 italic">No schedules added yet.</p>
      ) : (
        <div className="mt-8">
          <h3 className="text-xl font-semibold mb-4 text-center">Current Schedules</h3>
          <ul className="list-disc list-inside max-w-md mx-auto space-y-2">
            {schedules.map((schedule) => (
              <li key={schedule.id} className="text-center">
                {schedule.route} - {schedule.date} at {schedule.time}
              </li>
            ))}
          </ul>
        </div>
      )}
    </section>
  );
}
