"use client";

import { useState } from "react";
import BusRequestTable, { BusRequest } from "./BusRequestTable";
import BusScheduleManager, { BusSchedule } from "./BusScheduleManager";

export default function StaffDashboardPage() {
  const location = "Amarillo, Texas";

  const [requests, setRequests] = useState<BusRequest[]>([
    {
      id: 1,
      name: "John Doe",
      pickupLocation: "123 Main St",
      destination: "Community Center",
      date: "2024-07-01",
      time: "10:00",
      status: "Pending",
    },
  ]);
  const [schedules, setSchedules] = useState<BusSchedule[]>([]);

  const handleApprove = (id: number) => {
    setRequests((prev) =>
      prev.map((req) =>
        req.id === id ? { ...req, status: "Approved" } : req
      )
    );
  };

  const handleReject = (id: number) => {
    setRequests((prev) =>
      prev.map((req) =>
        req.id === id ? { ...req, status: "Rejected" } : req
      )
    );
  };

  const handleAddSchedule = (schedule: BusSchedule) => {
    setSchedules((prev) => [...prev, schedule]);
  };

  return (
    <main className="max-w-5xl mx-auto p-8 bg-white text-black rounded-lg shadow-lg space-y-12">
      <header className="mb-8 text-center">
        <h1 className="text-4xl font-extrabold tracking-tight mb-2">
          Staff Dashboard - {location}
        </h1>
        <div className="h-1 w-24 bg-black mx-auto rounded"></div>
      </header>

      <section aria-labelledby="bus-requests-title">
        <h2 id="bus-requests-title" className="text-3xl font-bold mb-6 text-center">
          Bus Requests
        </h2>
        <BusRequestTable
          requests={requests}
          onApprove={handleApprove}
          onReject={handleReject}
        />
      </section>

      <section aria-labelledby="bus-schedules-title">
        <h2 id="bus-schedules-title" className="text-3xl font-bold mb-6 text-center">
          Manage Bus Schedules
        </h2>
        <BusScheduleManager schedules={schedules} onAddSchedule={handleAddSchedule} />
      </section>
    </main>
  );
}
