"use client";

import React from "react";
import { Button } from "@/components/ui/button";

export interface BusRequest {
  id: number;
  name: string;
  pickupLocation: string;
  destination: string;
  date: string;
  time: string;
  status: "Pending" | "Approved" | "Rejected";
}

interface BusRequestTableProps {
  requests: BusRequest[];
  onApprove: (id: number) => void;
  onReject: (id: number) => void;
}

export default function BusRequestTable({ requests, onApprove, onReject }: BusRequestTableProps) {
  if (requests.length === 0) {
    return <p className="text-center text-gray-600 italic">No bus requests available.</p>;
  }

  return (
    <table className="w-full border-collapse border border-gray-300 rounded-md">
      <thead className="bg-gray-100">
        <tr>
          <th className="border border-gray-300 p-3 text-left">Name</th>
          <th className="border border-gray-300 p-3 text-left">Pickup Location</th>
          <th className="border border-gray-300 p-3 text-left">Destination</th>
          <th className="border border-gray-300 p-3 text-left">Date</th>
          <th className="border border-gray-300 p-3 text-left">Time</th>
          <th className="border border-gray-300 p-3 text-left">Status</th>
          <th className="border border-gray-300 p-3 text-center">Actions</th>
        </tr>
      </thead>
      <tbody>
        {requests.map((req, idx) => (
          <tr
            key={req.id}
            className={`text-center ${idx % 2 === 0 ? "bg-gray-50" : "bg-white"} hover:bg-gray-100 transition-colors`}
          >
            <td className="border border-gray-300 p-3 text-left">{req.name}</td>
            <td className="border border-gray-300 p-3 text-left">{req.pickupLocation}</td>
            <td className="border border-gray-300 p-3 text-left">{req.destination}</td>
            <td className="border border-gray-300 p-3 text-left">{req.date}</td>
            <td className="border border-gray-300 p-3 text-left">{req.time}</td>
            <td className="border border-gray-300 p-3 text-left font-semibold">
              <span
                className={
                  req.status === "Approved"
                    ? "text-green-600"
                    : req.status === "Rejected"
                    ? "text-red-600"
                    : "text-yellow-600"
                }
              >
                {req.status}
              </span>
            </td>
            <td className="border border-gray-300 p-3 space-x-3">
              {req.status === "Pending" && (
                <>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => onApprove(req.id)}
                    className="transition-colors hover:bg-black hover:text-white"
                  >
                    Approve
                  </Button>
                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={() => onReject(req.id)}
                    className="transition-colors hover:bg-black hover:text-white"
                  >
                    Reject
                  </Button>
                </>
              )}
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
